import strings from "../components/Localisation";
import cookie from "react-cookies";
import {PATH_MAIN, PATH_FACEL} from "./Paths";

export const LANGUAGE_RU = "ru";
export const LANGUAGE_EN = "en";
export const LANGUAGE_FR = "fr";
export const LANGUAGE_DE = "de";
export const LANGUAGE_ES = "es";
export const LANGUAGE_IT = "it";

export const TYPE_LANGUAGE = "language"

export class LanguagesUtils {

    changeLanguage(lang) {
        strings.setLanguage(lang);
        cookie.save(TYPE_LANGUAGE, strings.getLanguage());
    }

    getLanguageById(lang) {
        if (lang === LANGUAGE_EN) {
            return strings.header__languages_en;
        }

        else if (lang === LANGUAGE_RU) {
            return strings.header__languages_ru;
        }

        else if (lang === LANGUAGE_DE) {
            return strings.header__languages_de;
        }

        else if (lang === LANGUAGE_ES) {
            return strings.header__languages_es;
        }

        else if (lang === LANGUAGE_IT) {
            return strings.header__languages_it;
        }

        else {
            return strings.header__languages_fr;
        }
    }

    getLanguageText() {
        let language = this.getCurrentLanguage();

        if (language === LANGUAGE_EN) {
            return strings.header__languages_en;
        }

        else if (language === LANGUAGE_RU) {
            return strings.header__languages_ru;
        }

        else if (language === LANGUAGE_DE) {
            return strings.header__languages_de;
        }

        else if (language === LANGUAGE_ES) {
            return strings.header__languages_es;
        }

        else if (language === LANGUAGE_IT) {
            return strings.header__languages_it;
        }

        else {
            return strings.header__languages_fr;
        }
    }

    getCurrentLanguage() {
        let language = cookie.load(TYPE_LANGUAGE);
        return language? language : strings.getInterfaceLanguage();
    }

    getLanguagesArray() {
        return [
            {
                name: LANGUAGE_RU,
                link: PATH_MAIN,
                type: TYPE_LANGUAGE
            },
            {
                name: LANGUAGE_EN,
                link: PATH_MAIN,
                type: TYPE_LANGUAGE
            },
            {
                name: LANGUAGE_FR,
                link: PATH_MAIN,
                type: TYPE_LANGUAGE
            },
            {
                name: LANGUAGE_DE,
                link: PATH_MAIN,
                type: TYPE_LANGUAGE
            },
            {
                name: LANGUAGE_ES,
                link: PATH_MAIN,
                type: TYPE_LANGUAGE
            },
            {
                name: LANGUAGE_IT,
                link: PATH_MAIN,
                type: TYPE_LANGUAGE
            }
        ]
    }
}