import React, { Component} from 'react';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../utils/Amplitude";
import {GRADIENT_LIGHT_BLUE, GRADIENT_LIGHT_ROSE, GRADIENT_LIGHT_YELLOW} from "../../utils/Gradients";
let classNames = require('classnames');

export default class TeamCard extends Component {

    constructor(props) {
        super(props);
        this.cardId = nextId();
    }

    componentDidMount() {
        let link = this;
        let card = document.getElementById(this.cardId);

        card.addEventListener('click', function (event) {
            event.preventDefault();

            if (link.props.path) {
                sendAmplitudeData("Media: opening " + link.props.path);
                window.open(link.props.path, '_blank');
            }
        })
    }

    render() {
        let className = classNames('media_card', {
            'media_card__marginal': this.props.isMargin === true,
            'media_card__active': this.props.src,
            'media_card__background_secondary_light_yellow': this.props.gradient === GRADIENT_LIGHT_YELLOW,
            'media_card__background_secondary_light_rose': this.props.gradient === GRADIENT_LIGHT_ROSE,
            'media_card__background_secondary_light_blue': this.props.gradient === GRADIENT_LIGHT_BLUE,

        });

        return (
                <div className={className} id={this.cardId}>
                    <div className={"media_card__header"}>
                        <img className={"media_card__image"} src={this.props.src} />

                        <div className={"team_card__title_desc"}>
                            <div className={"media_card__title"}>{this.props.title}</div>
                            <div className={"team_card__description"}>{this.props.role}</div>
                        </div>
                    </div>

                    <div className={"team_card__experience"}>
                        {
                            this.props.experience? <div>{this.props.experience}</div> : <div/>
                        }
                        {
                            this.props.email? <div>{"\n" + this.props.email}</div> : <div/>
                        }
                    </div>
                </div>
        )
    }
}