import React, { Component} from 'react';
import MenuItem from "../fullsize/MenuItem";
import {
    PATH_ABOUT,
    PATH_APPLICATIONS,
    PATH_CONTACT,
    PATH_LANGUAGES,
    PATH_MAIN,
    PATH_STUFF, PATH_TEAM
} from "../../../../utils/Paths";
import strings from "../../../Localisation";
import LanguageItem from "../LanguageItem";
import nextId from "react-id-generator";
import cookie from "react-cookies";
import {sendAmplitudeData} from "../../../../utils/Amplitude";
import MobileMenuItem from "./MobileMenuItem";
import {Biglink} from "../../../base/Biglink";
import {LanguagesUtils} from "../../../../utils/LanguagesUtils";
let classNames = require('classnames');

export default class MobileMenu extends Component {
    constructor(props) {
        super(props);

        this.languages = new LanguagesUtils()
        let language = this.languages.getCurrentLanguage()

        this.state = {
            language: language,
            width: 2000
        };

        this.languages.changeLanguage(language);
    }

    componentDidMount() {
        this.setState({
            width: document.body.clientWidth
        });
    }

    render() {
        let className = classNames('header_menu__mobile', {
            'header_menu__mobile__active': this.props.isActive === true,
        });

        return (
                <div className={className}>
                    <div className={"header_menu__mobile__top"}>
                        <div className={"header_menu__mobile__close"}>
                            <img className={"header_menu__mobile__close_icon"} src={""} />
                        </div>
                    </div>
                    <div className={"header_menu__mobile__list"}>

                        <MobileMenuItem
                            type={"main"}
                            path={PATH_MAIN}
                            text={strings.header__main}/>

                        <MobileMenuItem
                            type={"ordinary"}
                            path={PATH_TEAM}
                            text={strings.footer__about_team}/>

                        <MobileMenuItem
                            type={"ordinary"}
                            path={PATH_CONTACT}
                            text={strings.header__contact}/>

                        <MobileMenuItem
                            type={"ordinary"}
                            path={PATH_STUFF}
                            text={strings.header__stuff}/>

                        <MobileMenuItem
                            type={"language"}
                            path={PATH_LANGUAGES}
                            text={this.languages.getLanguageText()}/>

                    </div>
                </div>
            );
    }
}