import React, { Component} from 'react';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../utils/Amplitude";
import strings from "../Localisation";
import cookie from "react-cookies";
import {LanguagesUtils, TYPE_LANGUAGE} from "../../utils/LanguagesUtils";
import {
    TYPE_FACEBOOK,
    TYPE_INSTAGRAM,
    TYPE_LINKEDIN,
    TYPE_ORDINARY, TYPE_PATREON,
    TYPE_TELEGRAM,
    TYPE_TWITTER, TYPE_VKONTAKTE
} from "../../utils/Links";
let classNames = require('classnames');

export class Biglink extends Component {

    constructor(props) {
        super(props);

        this.buttonId = nextId();
        this.languages = new LanguagesUtils();
    }

    changeLanguage(lang) {
        this.languages.changeLanguage(lang);
        this.setState({ language: lang });
        this.props.handle();
    }

    componentDidMount() {
        let element = document.getElementById(this.buttonId);
        let link = this;

        element.addEventListener('click', function (event) {
            event.preventDefault();

            if (link.props.type === TYPE_LANGUAGE) {
                sendAmplitudeData("Biglink: changing language to " + link.props.text);
                link.changeLanguage(link.props.text)
            }

            else {
                sendAmplitudeData("Biglink: opening " + link.props.path);

                const openWhere = (link.props.path[0] === "/")? "_self" : "_blank";
                window.open(link.props.path, openWhere);
            }
        })
    }

    render() {
        let thing = this.props.language? this.props.language : "";

        let className = classNames('biglink', {
            'link__ordinary': this.props.type === TYPE_ORDINARY,
            'link__instagram': this.props.type === TYPE_INSTAGRAM,
            'link__telegram': this.props.type === TYPE_TELEGRAM,
            'link__facebook': this.props.type === TYPE_FACEBOOK,
            'link__vkontakte': this.props.type === TYPE_VKONTAKTE,
            'link__patreon': this.props.type === TYPE_PATREON,
            'link__linkedin': this.props.type === TYPE_LINKEDIN,
            'link__twitter': this.props.type === TYPE_TWITTER,
            'link__active': thing === this.props.text
        });

        return (
            <div className={className} id={this.buttonId}>
                {
                    (this.props.type === TYPE_LANGUAGE)?
                            this.languages.getLanguageById(this.props.text)
                            :
                            this.props.text
                }

            </div>
        )
    }
}