import React, { Component} from 'react';
import MenuItem from './MenuItem';
import cookie from "react-cookies";
import strings from "../../../Localisation";
import Logo from './Logo'
import ShortForm from '../ShortForm'
import nextId from "react-id-generator";
import LanguageItem from "../LanguageItem";
import {sendAmplitudeData} from "../../../../utils/Amplitude";

import {
    PATH_ABOUT,
    PATH_APPLICATIONS,
    PATH_CONTACT,
    PATH_LANGUAGES,
    PATH_MAIN,
    PATH_STUFF, PATH_TEAM
} from "../../../../utils/Paths";
import {LanguagesUtils} from "../../../../utils/LanguagesUtils";

export default class FullsizeHeader extends Component {
    constructor(props) {
        super(props);

        this.languages = new LanguagesUtils()
        let language = this.languages.getCurrentLanguage()

        this.state = {
            language: language,
            width: 2000
        };

        this.languages.changeLanguage(language);
    }

    componentDidMount() {
        this.setState({
            width: document.body.clientWidth
        });
    }

    render() {
        return (
            <div className={"coating"}>
                <div className={"header_up_space"}/>

                <div className={"header"}>
                    <Logo/>

                    <div className={"header_menu"}>
                        <MenuItem
                            type={"main"}
                            path={PATH_MAIN}
                            text={strings.header__main}/>

                        <MenuItem
                            type={"ordinary"}
                            path={PATH_TEAM}
                            text={strings.footer__about_team}/>

                        <MenuItem
                            type={"ordinary"}
                            path={PATH_CONTACT}
                            text={strings.header__contact}/>

                        <MenuItem
                            type={"ordinary"}
                            path={PATH_STUFF}
                            text={strings.header__stuff}/>

                        <MenuItem
                            type={"language"}
                            path={PATH_LANGUAGES}
                            text={this.languages.getLanguageText()}/>

                    </div>
                </div>
            </div>
        )
    }
}