import React, { Component} from 'react';
import MobileHeader from "./mobile/MobileHeader";
import FullsizeHeader from "./fullsize/FullsizeHeader";

export default class Header extends Component {

    componentDidMount() {
        let link = this;
        link.isRenderedFullsize = window.innerWidth > 600;

        window.addEventListener("resize", function(event) {
            let isNowMobile = window.innerWidth <= 600;

            if ((link.isRenderedFullsize && isNowMobile) || (!link.isRenderedFullsize && !isNowMobile)) {
                link.isRenderedFullsize = !isNowMobile;
                link.forceUpdate();
            }
        });
    }

    render() {
        return (
            <div className={"coating"}>
                {
                    (window.innerWidth > 600)?
                        <FullsizeHeader />
                        :
                        <MobileHeader />
                }
            </div>
        )
    }
}