import React, { Component} from 'react';
import strings from "../../Localisation";
import VideoFunctional from "../../base/video/VideoFunctional";
import Space from "../../base/functional/Space";
import {FACEL_HEALTH__TIREDNESS_VIDEO, FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO} from "../../../utils/Videos";
import PhotoFunctional from "../../base/photo/PhotoFunctional";
import {LANGUAGE_EN, LANGUAGE_RU, LanguagesUtils} from "../../../utils/LanguagesUtils";

export default class Health extends Component {

    constructor(props) {
        super(props);

        this.languages = new LanguagesUtils();
        this.language = this.languages.getCurrentLanguage();
    }

    getHealthPhotosArray() {
        let array = [
            "Health_param_details.png",
            "Health_main_screen.png",
            "Health_organisations.png",
            "Health_pastime.png",
        ]

        let language = (this.language === LANGUAGE_RU)? LANGUAGE_RU : LANGUAGE_EN;

        return array.map(function(value) {
            return "/images/main/shots/" + language + "/health/" + value;
        });
    }

    render() {
        return (
           <div>
               <Space />
               <PhotoFunctional
                   array={this.getHealthPhotosArray()}
                   title={strings.health__title}
                   description={strings.health__description} />

           </div>
        )
    }
}