import React, { Component} from 'react';
import strings from "../../Localisation";
import VideoFunctional from "../../base/video/VideoFunctional";
import Space from "../../base/functional/Space";
import {FACEL_HEALTH__ORGANISATIONS_VIDEO, FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO} from "../../../utils/Videos";
import PhotoFunctional from "../../base/photo/PhotoFunctional";
import {LANGUAGE_EN, LANGUAGE_RU, LanguagesUtils} from "../../../utils/LanguagesUtils";

export class Business extends Component {

    constructor(props) {
        super(props);
        this.languages = new LanguagesUtils();
        this.language = this.languages.getCurrentLanguage();
    }

    getPhotos() {
        let array = [
            "Admin_details.png",
            "Admin_people_list.png",
            "Admin_profile_screen.png"
        ]

        let language = (this.language === LANGUAGE_RU)? LANGUAGE_RU : LANGUAGE_EN;

        return array.map(function(value) {
            return "/images/main/shots/" + language + "/admin/" + value;
        });
    }

    render() {
        return (
            <div className={"coating"}>
                <Space />
                <PhotoFunctional
                    array={this.getPhotos()}
                    title={strings.business__title}
                    description={strings.business__description} />
            </div>
        )
    }
}