import React, { Component} from 'react';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../../utils/Amplitude";
import {
    TYPE_FACEBOOK,
    TYPE_INSTAGRAM, TYPE_LINKEDIN,
    TYPE_ORDINARY,
    TYPE_PATREON,
    TYPE_TELEGRAM, TYPE_TWITTER,
    TYPE_VKONTAKTE
} from "../../../utils/Links";
let classNames = require('classnames');

export default class Item extends Component {

    constructor(props) {
        super(props);

        this.buttonId = nextId();
    }

    componentDidMount() {
        let thing = document.getElementById(this.buttonId);
        let link = this;

        thing.addEventListener('click', function(event) {
            event.preventDefault();

            sendAmplitudeData("Footer: opened " + link.props.path);

            const openWhere = (link.props.path[0] === "/")? "_self" : "_blank";
            window.open(link.props.path, openWhere);
        });
    }

    render() {
        let className = classNames('footer_item', {
            'link__ordinary': this.props.type === TYPE_ORDINARY,
            'link__instagram': this.props.type === TYPE_INSTAGRAM,
            'link__telegram': this.props.type === TYPE_TELEGRAM,
            'link__facebook': this.props.type === TYPE_FACEBOOK,
            'link__vkontakte': this.props.type === TYPE_VKONTAKTE,
            'link__patreon': this.props.type === TYPE_PATREON,
            'link__linkedin': this.props.type === TYPE_LINKEDIN,
            'link__twitter': this.props.type === TYPE_TWITTER
        });

        return (
            <p id={this.buttonId} className={className}>{this.props.text}</p>
        )
    }
}