import React, { Component} from 'react';

export default class Text extends Component {
    render() {
        let text = this.props.text.split('\n').map((item, i) => {
            return <div className={"p_text"} key={i}>{item.replace("NBSP", "\n")}</div>;
        });

        return (
            <div className={"functional__text"}>{text}</div>
        )
    }
}