import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {Biglinks} from "../base/Biglinks";
import Space from "../base/functional/Space";
import { Links } from "../../utils/Links";
import {LanguagesUtils} from "../../utils/LanguagesUtils";
import strings from "../Localisation";
import queryString from "query-string";
import metatags from "../Metatags";
import {Helmet} from "react-helmet";

export class Contact extends Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    checkLanguage() {
        let params = queryString.parse(this.props.location.search)
        let languages = new LanguagesUtils()
        let language = languages.getCurrentLanguage()

        if (params.language) {
            language = params.language
        }

        languages.changeLanguage(language)
        strings.setLanguage(language)
        metatags.setLanguage(language)

        return languages
    }

    render() {
        let languages = this.checkLanguage()
        let links = new Links();

        let languageName = languages.getCurrentLanguage();
        let title = metatags.index_title;
        let keywords = metatags.index_keyword;
        let description = metatags.index_description;
        let image = "https://facel.tech/images/seo/" + languages.getCurrentLanguage() + ".png";

        return (
            <div className={"app"}>
                <Helmet>
                    <title>{title}</title>
                    <meta content={title} name="title"/>
                    <meta content={title} property="og:title"/>
                    <meta content={title} property="twitter:title"/>
                    <meta content={description} name="description"/>
                    <meta content={description} property="og:description"/>
                    <meta content={description} property="twitter:description"/>
                    <meta content={keywords} name="keywords"/>
                    <meta content={image} property="og:image"/>
                    <meta content={image} property="twitter:image"/>
                    <meta content={languageName} name="og:locale"/>
                    <meta content={languageName} name="language"/>
                </Helmet>
                <Header />
                <Space />
                <Biglinks array={links.getContactArray()} />
                <Space />
                <Footer />
            </div>
        )
    }
}