import React, { Component } from 'react';
let classNames = require('classnames');

export class Column extends Component {
    render() {
        let className = classNames('footer_column', {
            'footer_column__ending': this.props.isEnding === true,
        });

        return (
            <div className={className} id={this.props.id}>
                <div className="footer_column__title">
                    {this.props.title}
                </div>
                <div className="footer_column__content">
                    {this.props.children}
                </div>
            </div>
        )
    }
}
