import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {BrowserRouter, Route} from 'react-router-dom'
import Total from './components/main/Total';
import {Screen} from './components/team/Screen'
import {About} from './components/biglinks/About'
import * as serviceWorker from './serviceWorker';
import { initAmplitude } from './utils/Amplitude';
import {Contact} from "./components/biglinks/Contact";
import {Stuff} from "./components/biglinks/Stuff";

import {
    PATH_MAIN,
    PATH_ABOUT,
    PATH_CONTACT,
    PATH_CONTROLLER,
    PATH_AGREEMENT,
    PATH_STUFF,
    PATH_TEAM,
    PATH_POLICY,
    PATH_INDEX,
    PATH_LANGUAGES
} from "./utils/Paths";
import {Controller} from "./components/application/controller/Controller";
import Policy from "./components/policy/Policy";
import Agreement from "./components/agreement/Agreement";
import {Languages} from "./components/biglinks/Languages";

initAmplitude();

ReactDOM.render(
    <div className={"coating"}>
        <BrowserRouter basename={process.env.PUBLIC_URL}>
            <div>
                <Route exact path={PATH_MAIN} component={ Total } />
                <Route path={PATH_INDEX} component={ Total } />
                <Route path={PATH_TEAM} component={ Screen } />
                <Route path={PATH_ABOUT} component={ About } />
                <Route path={PATH_CONTACT} component={ Contact } />
                <Route path={PATH_STUFF} component={ Stuff } />
                <Route path={PATH_CONTROLLER} component={ Controller } />
                <Route path={PATH_POLICY} component={ Policy } />
                <Route path={PATH_AGREEMENT} component={ Agreement } />
                <Route path={PATH_LANGUAGES} component={ Languages } />
            </div>
        </BrowserRouter>
    </div>,
    document.getElementById('root')
);
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more biglinks service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
