import React, { Component} from 'react';
import {Biglink} from "./Biglink";
import FullsizeHeader from "../main/header/fullsize/FullsizeHeader";
import {LanguagesUtils} from "../../utils/LanguagesUtils";

export class Biglinks extends Component {

    constructor(props) {
        super(props);

        this.languages = new LanguagesUtils()
    }

    handleStateUpdate() {
        this.setState({});
        this.props.handle();
    }

    getLinks() {
        let handleStateUpdate = this.handleStateUpdate;
        let language = this.languages.getCurrentLanguage()

        return this.props.array.map(element =>
            <Biglink
                language={language}
                handle={handleStateUpdate.bind(this)}
                type={element.type}
                path={element.link}
                text={element.name}/>
        );
    }

    render() {
        return (
            <div className={"biglinks"}>{ this.getLinks() }</div>
        )
    }
}